import axios from 'axios';
import Cache from './cache';
import store from '../ducks';

// 20
const cache = new Cache(20);

// Create intercepting get function which returns cached promise,
const caching = (config, data) => {
  let key = config.url + '?' + JSON.stringify(config.params || {});
  cache.set(key, data);
};

// 
const errorHandler = (message = '') => {
  const locale = store.state.locale;
  store.dispatch({
    type: 'showModal',
    modal: {
      title: locale === 'zh' ? '' : 'Sorry',
      message: message,
      buttons: [
        {
          text: locale === 'zh' ? '' : 'Confirm',
          onClick: () => {
            store.dispatch('hideModal');
          },
        },
      ],
    },
  });
};

axios.defaults.timeout = 20000;
axios.defaults.withCredentials = true;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';

// 
// Success Action
let count = 0;

// Add request interceptor
axios.interceptors.request.use(
  config => {
    // Action 
    if (config.global !== false) {
      //  count ( count-1)
      // Ajax
      // : ->TOAST->...
      count = Math.max(count, 0);
      count = count + 1;
      if (count === 1) {
        store.dispatch({
          type: 'showToast',
          toast: {
            icon: 'loading',
          },
        });
      }
    }
    return config;
  },
  error => {
    count = -1;
    errorHandler('');
    return Promise.reject(error);
  }
);
// Add response interceptor
axios.interceptors.response.use(
  ({ config, data = { code: 500 } }) => {
    // code
    if (typeof data.code === 'undefined') {
      data.code = data.isError ? 500 : 0;
    }
    // code
    if (/^\d+$/.test(data.code)) {
      data.code = Number(data.code);
    }

    //  
    if (config.global !== false) {
      count = count - 1;
      if (data.code !== 0) {
        count = -1;
        store.dispatch('hideToast');
        errorHandler(data.message || '');
      }
      // Action Toast
      else if (count === 0) {
        store.dispatch('hideToast');
      }
    }

    // 
    if (data.code !== 0) {
      return Promise.reject(data);
    }

    //  - GET
    if (config.method === 'get' && config.cache === true) {
      caching(config, data);
    }

    // 
    return data;
  },
  error => {
    if (axios.isCancel(error)) {
      count = count - 1;
      if (count === 0) {
        store.dispatch('hideToast');
      }
    }
    // 
    else {
      count = -1;
      store.dispatch('hideToast');
      const locale = store.state.locale;
      errorHandler(locale === 'zh' ? '' : 'Network ErrorPlease try again.');
    }
    return Promise.reject(error);
  }
);

// GET  
let get = axios.get;
axios.get = function(url, options = {}) {
  let params = options.params || {};
  let key = url + '?' + (JSON.stringify(params) || '');
  let data = cache.get(key);
  if (options.cache === true && data) {
    return Promise.resolve(data);
  }
  return get(...arguments);
};
